.home-container {
  padding: 40px 20px;
  color: #fff;
  display: flex;
  flex-direction: column;
  gap: 40px;
  background: radial-gradient(circle at top left, #0a192f 0%, #020c1b 100%);
}

/* ÿßŸÑŸÉÿ±ÿ™ ÿßŸÑÿ≤ÿ¨ÿßÿ¨Ÿä ŸÑŸÑŸÅŸàÿ±ŸÖ */
.form-glass {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  padding: 28px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(100, 255, 218, 0.1);
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.form-glass h2 {
  color: #64ffda;
  text-align: center;
  margin-bottom: 20px;
  font-size: 1.5rem;
  text-shadow: 0 0 10px rgba(100, 255, 218, 0.3);
}

/* ÿßŸÑÿ≠ŸÇŸàŸÑ */
.glass-form {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
  gap: 16px;
}

.glass-form input {
  padding: 12px 16px;
  border-radius: 10px;
  border: 2px solid rgba(100, 255, 218, 0.2);
  background: rgba(255, 255, 255, 0.05);
  color: #fff;
  font-size: 15px;
  transition: all 0.3s ease;
  backdrop-filter: blur(8px);
}

.glass-form input:focus {
  outline: none;
  border-color: #64ffda;
  box-shadow: 0 0 8px rgba(100, 255, 218, 0.3);
}

/* ÿ≤ÿ± ÿßŸÑÿ•ÿ∂ÿßŸÅÿ© */
.btn-submit {
  grid-column: 1 / -1;
  padding: 14px;
  background: linear-gradient(135deg, #64ffda, #00d4aa);
  color: #0a192f;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-submit:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 25px rgba(100, 255, 218, 0.4);
}
/* üîπ ŸÖÿ±ŸÉÿ≤ ŸÉŸÑ ÿπŸÜÿßÿµÿ± ÿßŸÑŸÜŸÖŸàÿ∞ÿ¨ */
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 40px 0;
}

/* ‚úÖ ÿßŸÑÿµŸÜÿØŸàŸÇ ÿßŸÑÿ≤ÿ¨ÿßÿ¨Ÿä */
.form-glass {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  padding: 30px;
  width: 100%;
  max-width: 420px;
  text-align: center;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(100, 255, 218, 0.2);
  backdrop-filter: blur(10px);
}

/* üßæ ÿßŸÑÿπŸÜŸàÿßŸÜ */
.form-glass h2 {
  color: #64ffda;
  text-shadow: 0 0 12px rgba(100, 255, 218, 0.4);
  margin-bottom: 25px;
}

/* üéØ ÿ™ŸÜÿ≥ŸäŸÇ ÿßŸÑÿ≠ŸÇŸàŸÑ */
.glass-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.glass-form input {
  width: 90%;
  max-width: 350px;
  padding: 12px 16px;
  border-radius: 12px;
  border: 2px solid rgba(100, 255, 218, 0.2);
  background: rgba(255, 255, 255, 0.08);
  color: #fff;
  font-size: 15px;
  text-align: center;
  transition: 0.3s ease;
  backdrop-filter: blur(10px);
}

.glass-form input:focus {
  outline: none;
  border-color: #64ffda;
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 10px rgba(100, 255, 218, 0.3);
}

/* üü© ÿ≤ÿ± ÿßŸÑÿ•ÿ±ÿ≥ÿßŸÑ */
.btn-submit {
  width: 90%;
  max-width: 350px;
  padding: 12px;
  border: none;
  border-radius: 10px;
  background: linear-gradient(135deg, #64ffda, #00d4aa);
  color: #0a192f;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
}

.btn-submit:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 25px rgba(100, 255, 218, 0.4);
}

.btn-submit:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* üì± ÿπŸÑŸâ ÿßŸÑŸÖŸàÿ®ÿßŸäŸÑ */
@media (max-width: 600px) {
  .form-glass {
    width: 90%;
    padding: 25px 20px;
  }

  .glass-form input,
  .btn-submit {
    width: 100%;
  }
}
/* üíª ÿπŸÑŸâ ÿßŸÑÿ¥ÿßÿ¥ÿßÿ™ ÿßŸÑŸÉÿ®Ÿäÿ±ÿ© */
@media (min-width: 1200px) {
  .form-glass {
    max-width: 600px; /* ŸÉÿßŸÜÿ™ 420px */
    padding: 40px 50px;
    border-radius: 24px;
    box-shadow: 0 12px 40px rgba(0, 0, 0, 0.4);
  }

  .form-glass h2 {
    font-size: 1.8rem;
    margin-bottom: 30px;
  }

  .glass-form input {
    max-width: 450px;
    font-size: 17px;
    padding: 14px 20px;
  }

  .btn-submit {
    max-width: 450px;
    padding: 14px;
    font-size: 17px;
    border-radius: 12px;
  }
}
