/* üîπ ÿ≤ÿ± ŸÅÿ™ÿ≠ ÿßŸÑŸÇÿßÿ¶ŸÖÿ© */
.toggle-btn {
  position: fixed;
  top: 20px;
  left: 20px;
  background: rgba(255, 255, 255, 0.08);
  color: #64ffda;
  border: 2px solid rgba(100, 255, 218, 0.2);
  border-radius: 12px;
  font-size: 24px;
  cursor: pointer;
  padding: 8px 14px;
  z-index: 1001;
  backdrop-filter: blur(10px);
  transition: 0.3s ease;
}

.toggle-btn:hover {
  background: rgba(100, 255, 218, 0.15);
  box-shadow: 0 0 15px rgba(100, 255, 218, 0.3);
  transform: scale(1.05);
}

/* üîπ ÿßŸÑŸÇÿßÿ¶ŸÖÿ© ÿßŸÑÿ¨ÿßŸÜÿ®Ÿäÿ© */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh; /* ‚¨ÖÔ∏è ÿ™ŸÖÿ™ÿØ ŸÖŸÜ ÿ£ŸàŸÑ ŸÑÿ¢ÿÆÿ± ÿßŸÑÿµŸÅÿ≠ÿ© */
  width: 260px; /* ÿπÿ±ÿ∂ ÿßŸÑÿ¥ÿ±Ÿäÿ∑ */
  background: rgba(10, 25, 47, 0.9);
  border-right: 1px solid rgba(100, 255, 218, 0.15);
  border-radius: 0 20px 20px 0;
  backdrop-filter: blur(12px);
  padding: 80px 20px 20px; /* padding ÿπŸÑŸàŸä ÿπÿ¥ÿßŸÜ Ÿäÿ®ÿπÿØ ÿπŸÜ ÿßŸÑÿ≤ÿ± */
  box-shadow: 5px 0 25px rgba(0, 0, 0, 0.4);
  transition: all 0.4s ease;
  z-index: 1000;
}

/* ÿπŸÜÿØ ÿßŸÑÿ•ÿ∫ŸÑÿßŸÇ */
.navbar.closed {
  transform: translateX(-100%);
  opacity: 0;
}

/* ÿπŸÜÿØ ÿßŸÑŸÅÿ™ÿ≠ */
.navbar.open {
  transform: translateX(0);
  opacity: 1;
}

/* üîπ ÿßŸÑÿ±Ÿàÿßÿ®ÿ∑ */
.nav-links {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.nav-links li {
  text-align: left;
}

.nav-links a {
  color: #fff;
  text-decoration: none;
  font-size: 17px;
  font-weight: 600;
  letter-spacing: 0.5px;
  padding: 12px 16px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(100, 255, 218, 0.1);
  display: flex;
  align-items: center;
  gap: 10px;
  transition: all 0.3s ease;
}

.nav-links a:hover {
  background: rgba(100, 255, 218, 0.15);
  color: #64ffda;
  transform: scale(1.03);
  box-shadow: 0 0 12px rgba(100, 255, 218, 0.25);
}

/* üîπ ÿ™ÿ¨ÿßŸàÿ® ÿßŸÑŸÖŸàÿ®ÿßŸäŸÑ */
@media (max-width: 768px) {
  .navbar {
    width: 80%;
    max-width: 280px;
  }

  .toggle-btn {
    top: 15px;
    left: 15px;
  }

  .nav-links a {
    font-size: 15px;
    padding: 10px 12px;
  }
}
/* üîπ ÿ≤ÿ± ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿÆÿ±Ÿàÿ¨ */
.logout-btn {
  width: 100%;
  margin-top: 40px;
  padding: 12px 16px;
  background: linear-gradient(135deg, #ff6b6b, #ee5253);
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(255, 107, 107, 0.3);
}

.logout-btn:hover {
  background: linear-gradient(135deg, #ee5253, #ff6b6b);
  transform: scale(1.05);
  box-shadow: 0 8px 25px rgba(255, 107, 107, 0.4);
}
